@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --font-primary: 'Ubuntu', sans-serif;
  --color-primary: #e2e2e2;
  --color-secondary: rgb(32, 255, 144);
  --color-gray1: #2c2e3a;
  --color-gray2: #494c58;
  --color-gray3: #2a2b30;
  --color-bg: #181a22;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Ubuntu', sans-serif;
  font-family: var(--font-primary);
  background-color: #181a22;
  background-color: var(--color-bg);
  color: #fff;
  width: 100%;
  height: 100%;
  font-weight: 300;
  overflow-x: hidden;
}

.wave {
  width: 1200px;
  height: 1250px;
  position: fixed;
  top: 0%;
  left: 0%;
  margin-left: -500px;
  margin-top: -500px;
  border-radius: 30%;
  background: #1c1c29bf;
  -webkit-animation: wave 15s infinite linear;
          animation: wave 15s infinite linear;
  z-index: -1;
}

section {
  margin: 1em;
  padding: 1em;
  border-radius: 5px;
}

h1,
h2 {
  color: rgb(32, 255, 144);
  color: var(--color-secondary);
  font-size: clamp(1.2rem, 8vw, 1.6rem);
  font-weight: 500;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

body::-webkit-scrollbar-thumb {
  background: #e2e2e2;
  background: var(--color-primary);
  border-radius: 5px;
}

body::-webkit-scrollbar {
  width: 8px;
  background-color: #2c2e3a;
  background-color: var(--color-gray1);
}

@-webkit-keyframes wave {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes wave {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#hero {
  display: flex;
  justify-content: space-between;
  padding: 5em 3em;
  flex-direction: row;
}

#projects {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

#stack {
  text-align: center;
}

.sheesh {
  display: flex;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  font-style: italic;
}

@media screen and (max-width: 968px) {
  #hero {
    flex-direction: column;
    justify-content: center;
  }
}

